import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/article/:name',
      name: 'article',
      component: resolve => {
        // require.ensure 是 Webpack 的特殊语法，用来设置 code-split point
        // （代码分块）
        require.ensure(['../view/article.vue'], () => {
          resolve(require('../view/article.vue'))
        })
      }
    },
    {
      path: '/List/:type',
      name: 'blogList',
      component: resolve => {
        // require.ensure 是 Webpack 的特殊语法，用来设置 code-split point
        // （代码分块）
        require.ensure(['../view/blogList.vue'], () => {
          resolve(require('../view/blogList.vue'))
        })
      }
    },
    {
      path: '/aboutMe',
      name: 'aboutMe',
      component: resolve => {
        // require.ensure 是 Webpack 的特殊语法，用来设置 code-split point
        // （代码分块）
        require.ensure(['../view/aboutMe.vue'], () => {
          resolve(require('../view/aboutMe.vue'))
        })
      }
    }
  ]
})



