<template>
  <div class="main">
    <mu-appbar title="welcome to liujains" class="trans nav" :class="{'nav-hide': !open}">
      <mu-icon-button icon="menu" slot="left" @click="toggle()"/>
      <mu-icon-button class="color-white" slot="right" href="https://github.com/liujians" icon=":mudocs-icon-custom-github"/>
    </mu-appbar>
    <mu-drawer :open="open" :docked="docked" @close="toggle()" class="trans">
      <mu-appbar title="LJS">
      </mu-appbar>
      <mu-list>
          <mu-sub-header>MENU</mu-sub-header>
          <router-link to="/">
            <mu-list-item title="主页">
              <mu-icon slot="left" value="home"/>
            </mu-list-item>
          </router-link>
          <!--<mu-list-item title="建站笔记">
            <mu-icon slot="left" value="note"/>
          </mu-list-item>-->
          <router-link to="/aboutMe">
            <mu-list-item title="关于我&留言板">
              <mu-icon slot="left" value="assignment_ind"/>
            </mu-list-item>
          </router-link>
          <mu-divider />
          <mu-sub-header>NOTE</mu-sub-header>
          <router-link to="/List/demo">
            <mu-list-item title="开源Demo">
              <mu-icon slot="left" value="grade"/>
            </mu-list-item>
          </router-link>
          <router-link to="/List/blog">
            <mu-list-item title="技术博客">
              <mu-icon slot="left" value="book"/>
            </mu-list-item>
          </router-link>
          <a href="/planeGame/planeProject.html" target="_blank">
            <mu-list-item title="Doomed体验版">
              <mu-icon slot="left" value="games"/>
            </mu-list-item>
          </a>
        </mu-list>
    </mu-drawer>
    <div class="content" :class="{'nav-hide': !open}">
      <router-view></router-view>
    </div>
  </div>
</template>
<style lang="less">
  .main{
    .nav{
      position: fixed;
      top:0;
      left:0;
      right: 0;
      left: 256px;
      width: auto;
      &.nav-hide {
        left: 0;
      }
    }
  }
  .content{
    padding-top: 64px;
    padding-left: 256px;
    transition: all .45s cubic-bezier(.23,1,.32,1);
    &.nav-hide {
      padding-left: 0;
    }
  }
  .trans{
    transition: all .45s cubic-bezier(.23,1,.32,1);
  }
</style>
<script>
export default {
  name: 'app',
  data () {
    const desktop = isDesktop()
    return {
      str: '',
      open: desktop,
      desktop,
      docked: desktop
    }
  },
  methods: {
    toggle () {
      this.open = !this.open
    },
    changeNav () {
      const desktop = isDesktop()
      this.docked = desktop
      if (desktop === this.desktop) return
      if (!desktop && this.desktop && this.open) {
        this.open = false
      }
      if (desktop && !this.desktop && !this.open) {
        this.open = true
      }
      this.desktop = desktop
    }
  },
  created () {

  },
  mounted () {
    this.changeNav()
    this.handleResize = () => {
      this.changeNav()
    }
    window.addEventListener('resize', this.handleResize)
  },
  destroyed () {
    window.removeEventListener('resize', this.handleResize)
  }
}
function isDesktop () {
  return window.innerWidth > 993
}
</script>



